@breadcrumb_current_font_color: @color_gray;

.breadcrumb {
  .breadcrumb-item {
    > span {
      font-weight: bold;
      color: @breadcrumb_current_font_color;
    }
    > span.breadcrumb-item-next {
      margin: 0 4px;
    }
  }
}


